/** Ui **/
.header {
  width: 100%;
  height: 80px;
  background-color: rgba(145, 143, 143, 0.25);
  backdrop-filter: blur(1rem);
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 200;
  top: 0;
}
.header .logo {
  height: 80px;
  width: auto;
}
.header .logo img {
  max-height: 100%;
  max-width: 100%;
}

.icon_menu {
  width: 3rem;
  margin-right: 3rem;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
}
.icon_menu i {
  font-size: 2.7rem;
  cursor: pointer;
  position: absolute;
}

.menu {
  display: flex;
  justify-content: center;
  transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.options {
  display: flex;
}
.options .activ {
  background-color: #871111;
  border-radius: 0.5rem;
  backdrop-filter: blur(1rem);
  transition: color 300ms;
}
.options .activ .option p {
  color: #fff;
  font-size: 2rem;
  font-weight: 600;
}
.options .option {
  padding-left: 2rem;
  padding-right: 2rem;
  display: flex;
  align-items: center;
  position: relative;
  height: 4rem;
}
.options .option p {
  color: #fff;
  font-weight: 400;
  cursor: pointer;
  font-size: 2rem;
}
.options .option:hover {
  background-color: #871111;
  border: 0.1rem solid #333;
  border-radius: 0.5rem;
  backdrop-filter: blur(1rem);
  transition: color 300ms;
}
.options .option:hover p {
  color: #fff;
  font-size: 2rem;
  font-weight: 600;
}

.usser p {
  font-size: 2.5rem;
  color: #fff;
  letter-spacing: 0.1rem;
}

@media (max-width: 810px) {
  .header {
    padding: 0 2rem;
  }
  .header .logo {
    height: 80px;
    width: 100%;
  }
  .header .logo img {
    max-height: 100%;
    max-width: 90%;
  }
  .header .option {
    padding: 0 0.7rem 0 0.7rem !important;
  }
  .header .option i, .header .option p {
    font-size: 1.5rem !important;
  }
}
@media (max-width: 480px) {
  .header {
    padding: 0 1rem !important;
  }
  .header .menu {
    height: 0vh;
    width: 100vw;
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    background: rgba(135, 17, 17, 0.63);
    backdrop-filter: blur(1rem);
    transition: 1.5s;
    overflow: hidden;
  }
  .header .menu .options {
    display: block;
    width: fit-content;
    margin: 25vh auto 0 auto;
    text-align: center;
  }
  .header .menu .options .activ {
    color: #918f8f;
  }
  .header .menu .options .activ .option {
    background-color: #333;
    border: 0.1rem solid #918f8f;
    border-radius: 0.5rem;
    backdrop-filter: blur(1rem);
    transition: color 300ms;
  }
  .header .menu .options .activ .option i, .header .menu .options .activ .option p {
    font-size: 2rem !important;
  }
  .header .menu .options .option {
    font-size: 5rem;
    width: 100%;
    margin-bottom: 2rem;
  }
  .header .menu .options .option:hover {
    color: #918f8f;
    transition: 0.5s;
    background-color: #333;
    border: 0.1rem solid #918f8f;
    border-radius: 0.5rem;
    backdrop-filter: blur(1rem);
    transition: color 300ms;
  }
  .header .menu .options .option:hover p {
    font-size: 2rem !important;
  }
  .header .active {
    opacity: 1;
    transition: 1.5s;
    height: 100vh;
  }
  .header .icon_menu {
    display: inline-flex;
  }
  .header .usser {
    display: none;
  }
}
@media (min-width: 1280px) {
  .header {
    padding: 0 5rem;
  }
}
/** internas **/
.banner {
  position: relative;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  transition: all 0.5s ease-in-out;
  background-image: url(..//img/hero-1.jpg);
  animation: banner 28s infinite linear;
}
.banner .banner-content {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: rgba(51, 51, 51, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.banner .banner-info {
  width: 90vw;
}
.banner .banner-info p {
  color: #fff;
  text-align: center;
  font-size: 3rem;
  font-weight: 400;
}
@keyframes banner {
  0% {
    background-image: url(..//img/hero-1.jpg);
  }
  25% {
    background-image: url(..//img/hero-1.jpg);
  }
  26% {
    background-image: url(..//img/hero-2.jpg);
  }
  50% {
    background-image: url(..//img/hero-2.jpg);
  }
  51% {
    background-image: url(..//img/hero-3.jpg);
  }
  75% {
    background-image: url(..//img/hero-3.jpg);
  }
  76% {
    background-image: url(..//img/hero-4.jpg);
  }
  100% {
    background-image: url(..//img/hero-4.jpg);
  }
}

@media (max-width: 480px) {
  .banner-info p {
    font-size: 1.6rem !important;
  }
}
.servicios {
  display: flex;
