.contenedor-c {
    width: 90%;
    margin: 0px auto;
    display: flex;
    justify-content: center;
}
.form-cont {
    align-items: center;
    width: 640px;
    margin: 1rem;
}
.form-redes {
    width: 640px;
    margin: 1rem;
    font-size: 2rem;
    text-align: center;
    align-items: center;
    h4 {
        transform: scale(1.2);
    }
    p,a,label{
        color: $white;
        }
        i{
            color: $grisOscuro;
            font-size: 2.2rem;
        }
    .column-s {
        display: flex;
        justify-content: space-between;
        margin-bottom: 5rem;
        .s-row{
            margin: 1rem;
            &:hover{
                i{
                    color: $grisClaro;
                   transform: scale(1.2);
                }
                a{
                    color: $grisOscuro;
                   transform: scale(1.2 );
                }
            }
        }
    }
    .column-c{
        margin-bottom: 5rem;
    .c-row{
        margin: 2rem 0;
    }
    }
}
.contacto-bg {
    background-image: url(/build/img/euge-1.jpeg);
    height: 80vh;
    background-size: cover;
    background-repeat: no-repeat;
}
.formulario { 
    margin: -75rem auto 0 auto;
    width: 80%;
    height: 95%;
    padding: 2rem;
    .campo {
        display: flex;
        margin-bottom: 2rem;
        gap: 2rem;
        color: $black;
        display: block;
        .campo_label {
            flex: 0 0 9rem;
            text-align: right;
        }
        input{
            display: block;
            height: 5rem;
            width: 100%;
            background-color: $grisOscuroT;
            border-radius: .3rem;
            padding: 0 1rem;
            margin-top: .8rem;
            &:focus {
                border: 2px solid $redG;
                box-shadow: 0 0 1rem $red;
                color: $white;
            }
            &::placeholder{
                color: $white;
            }
        }
        .campo_field {
            flex: 1;
            outline: none;
            border: none;
        }
        .campo_field-textarea {
            height: 15rem;
            width: 100%;
            background-color: $grisOscuroT;
            border-radius: .3rem;
            padding: 0 1rem;
            margin-top: .8rem;
            &:focus {
                border: 1px solid $redG;
                box-shadow: 0 0 1rem $red;
                color: $white;
            }
            &::placeholder{
                color: $white;
            }
        }
    }
}

.boton_primario {
    background-color: $red;
    &:hover {
        background-color: $grisMedium;
    }
}
/* medias*/
@include tablet {
    .margin_bot{
        margin-bottom: 16rem;
    }
    .form-cont {
        align-items: center;
        margin-left: 0rem;
    }
    .contacto-bg{
        height: 90vh;
    }
    .formulario {
        margin: -90rem auto 0 auto;
        width: 95%;
        padding: 4rem;
        .campo {
            margin-bottom: 1rem;
            .campo_field {
                margin: .5rem;
            }
            .campo_field-textarea {
                height: 20rem;
            }
        }
    }
}
@include telefono {
    .contacto-bg{
        height: 100vh;
    }
    .formulario {
        margin: -72rem auto 0 auto;
        width: 80vw;
        padding: .5rem;
        .campo {
            margin-bottom: 1 rem;
            .campo_field {
                margin: .5rem;
                width: 95%;
            }
            .campo_field-textarea {
                height: 20rem;
                width: 95%;
            }
        }
    }
}
@include telefono-S {
    .formulario {
        margin: -60rem auto 0 auto;
        width: 80vw;
        padding: .5rem;
     }
}