.header {
    width: 100%;
    height: 80px;
    background-color: $grisClaroT;
    backdrop-filter: blur(1rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    z-index: 200;
    top: 0;
    .logo {
        height:80px;
        width:auto;
        img {
            max-height:100%;
            max-width:100%;
        }
    }
}
.icon_menu {
    width: 3rem;
    margin-right: 3rem;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    i{
        font-size: 2.7rem;
        cursor: pointer;
        position: absolute;
    }
}
.menu {
    display: flex;
    justify-content:center ;
    transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.options {
    display: flex;
    .activ {
        .option {
            p {
                color: $white;
                font-size: 2rem;
                font-weight:$h_black;
            }
        }
        background-color: $red ;

        border-radius: .5rem;
        backdrop-filter: blur(1rem);
        transition: color 300ms;
    }
    .option {
        padding-left: 2rem;
        padding-right: 2rem;
        display: flex;
        align-items: center;
        position: relative;
        height: 4rem;
        p {
            color: $white;
            font-weight: $h_delgada;
            cursor: pointer;
            font-size: 2rem;
        }
        &:hover{
            p {
                color: $white;
                font-size: 2rem;
                font-weight:$h_black;
            }
            background-color: $red;
            border: .1rem solid $grisOscuro;
            border-radius: .5rem;
            backdrop-filter: blur(1rem);
            transition: color 300ms;
        }
    }
}
.usser {
    p {
        font-size: 2.5rem;
        color: $white;
        letter-spacing: .1rem;
    }   
    
}
@include tablet {
    .header {
        padding: 0 2rem;
        .logo {
            height:80px;
            width:100%;
            img {
                max-height:100%;
                max-width:90%;
            }
        }
        .option {
            padding: 0 .7rem 0 .7rem !important;
            i ,p {
                font-size: 1.5rem  !important;
            }
        }
    }
}
@include telefono {
    .header {
        padding: 0 1rem !important;
        .menu {
            height: 0vh;
            width: 100vw;
            position: absolute;
            top: 80px;
            left: 0;
            right: 0;
            background: $redB;
            backdrop-filter: blur(1rem);
            transition: 1.5s;
            overflow: hidden;
            .options {
                display: block;
                width: fit-content;
                margin: 25vh auto 0 auto;
                text-align: center;
                .activ {
                    color: $grisClaro;
                    .option{
                        background-color: $grisOscuro;
                        border: .1rem solid $grisClaro;
                        border-radius: .5rem;
                        backdrop-filter: blur(1rem);
                        transition: color 300ms;
                        i,p {
                            font-size: 2rem !important;
                        }
                    }   
                }
                .option {
                    font-size: 5rem;
                    width: 100%;
                    margin-bottom: 2rem;
                    &:hover {
                        color: $grisClaro;
                        transition: .5s;
                        background-color: $grisOscuro;
                        border: .1rem solid $grisClaro;
                        border-radius: .5rem;
                        backdrop-filter: blur(1rem);
                        transition: color 300ms;
                        p {
                            font-size: 2rem !important;
                        }
                    }
                }
            }
        }
        .active {
            opacity: 1;
            transition: 1.5s;
            height: 100vh;  
        }
        .icon_menu {
            display: inline-flex;
        }
        .usser {
            display: none;
        }
    }
}
@include desktop {
    .header {
        padding: 0 5rem;
    }
}