.contenedor-s {
    margin: 0px auto;
    .somos {
        width: 100%;
        height: 100vh;
        background-image: url(/build/img/somos.jpg);
        background-attachment: fixed;
        background-position: center;
        background-size: cover;
        position: relative;
        overflow: hidden;
        text-align: center;
        .color {
            top: 0;
            width: 100%;
            height: 100%;
            background-color: $grisOscuroT;
        }
        .textos {
            padding: 20rem;
            p {
                text-align: justify;
            }
        }
        .skew {
            position: absolute;
            bottom: 0;
            left: 0;
            border-width:0 0 50vh 100vw;
            border-style: solid;
            border-color: transparent transparent $grisOscuro;
        }
    }
}
.contenedor {
    .mis-vis {
        width:100%;
        .rows {
            width:100%;
            display: flex;
            justify-content: center;
            .row {
                width: 100%;
                padding: 0 3rem;
            }
        }
    }
.cont-valores {
        max-width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .valor {
            width: 300px;
            height: 350px;
            margin: 2rem;
            overflow: hidden;
            .titulo {
                margin-top: 0;
                margin-bottom:  -2rem;
                padding: .5rem;
            }
            p {
                padding: 1rem;
                font-size: 1.4rem;
            }
            .contenido{
                justify-content: center;
                picture{
                    width: 10rem;
                    height: 10rem;
                    margin-left: 25%;
                }
            }
        }
    }
}
/* medias */

@include tablet {
    .contenedor-s {
        .somos {
            .textos{
                padding: 15rem;
            }
        }
    }
}
@include telefono {
    .contenedor-s {
        .somos {
            .textos {
                padding: 10rem 5rem;
            }
            .skew{
                border-width:0 0 30vh 100vw;
            }
        }
    }
    .contenedor {
        
        .mis-vis {
            margin-bottom: 1.5rem;
            .rows {
                flex-wrap: wrap;
            }
        }
    }
}    
@include telefono-S {
    .contenedor-s {
        .somos {
            .textos {
                padding: 7rem 5rem;

            }
            .skew{
                border-width:0 0 25vh 100vw;
            }
        }
    }
}
@include telefono-SS {
    .contenedor-s {
        .somos {
            .textos {
                padding: 7rem 3rem;

            }
            .skew{
                border-width:0 0 25vh 100vw;
            }
        }
    }
}

