.registros {
    width: 100rem;
    margin: 0px auto;
    background-color: $grisClaroT;  
    border-radius: 1rem;
    left: 50%;
    backdrop-filter: blur(1rem);
    border: 2px solid $grisOscuroT;
    box-shadow: 0 0 1rem $grisOscuroT;
    color: $white;

    .tabla {  
        table,td,th {
            margin: .5rem;
            padding: .5rem;
            border:none;
            img {
                height: 10  rem;
                width: 8.3rem;
            }
        }

    }

}