/** Media Queries **/
@mixin telefono-SS {
    @media (max-width: #{$telefonoSS}) {
        @content;
    }
}
@mixin telefono-S {
    @media (max-width: #{$telefonoS}) {
        @content;
    }
}

@mixin telefono {
    @media (max-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $white ;
    font-weight: $p_black;
    text-decoration: none;
    padding:1rem 1.5rem;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;
    border: none;
    cursor: pointer;
    &:hover {
        background-color: darken($bgColor,5);
    }
}