.registro {
    width: 60rem;
    margin: 0px auto;
        .campo{
            margin: 2rem;
            justify-content: center;
            display: flex;
            label{
                width: 80%;
            }
            input {
                display: block;
                height: 5rem;
                width: 100%;
                background-color: $grisMediumT;
                border-radius: .3rem;
                padding: 0 1rem;
                margin-top: .8rem;
                outline: none;
                border: none;
                &:focus {
                    border: 1px solid $redG;
                    box-shadow: 0 0 1rem $redG;
                }
                &::placeholder{
                    color: $white;
                }
            }
            img {
                width: 100%;
                height: 16rem;
            }
        }
}
