.d-mode {
    background-image: url(/build/img/logo_dim.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-color: $grisOscuro;
}
.form-log {
    width: 43rem;
    height: 52rem;
    position: absolute;
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
    border: 1px solid $grisOscuroT;
    border-radius: 1rem;
    form{
        height: 52rem;
        width: 43rem;
        background-color: $grisClaroT;
        position: absolute;
        transform: translate(-50%,-50%);
        top: 50%;
        left: 50%;
        border-radius: 1rem;
        backdrop-filter: blur(1rem);
        border: 2px solid $grisOscuroT;
        box-shadow: 0 0 4rem $grisClaroT;
        h1,label{
            color: $white;
            margin: 1rem;
            font-weight: $h_black;
            letter-spacing: .5rem;
        }
        .campo{
            margin: 2rem;
            justify-content: center;
            input {
                display: block;
                height: 5rem;
                width: 100%;
                background-color: $grisMediumT;
                border-radius: .3rem;
                padding: 0 1rem;
                margin-top: .8rem;
                outline: none;
                border: none;
                &:focus {
                    border: 1px solid $redG;
                    box-shadow: 0 0 1rem $redG;
                }
                &::placeholder{
                    color: $white;
                }
            }
        }
        .btn {
            margin: 2rem;
            input {
                margin-top: 5rem;
                width: 100%;
                background-color: $white;
                color: $grisOscuro;
                font-size: 2.2rem;
                font-weight: $p_black;
                padding: 1.5rem 0;
                border-radius: .5rem;
                cursor: pointer;
                &:hover{
                background-color: $grisClaroT;
                color: $white;
                border: 1px solid $redG;
                box-shadow: 0 0 1rem $redG;
                }
            }
        }

    }

}
.efooter {
    background-color: $grisClaro;
}