.servicios {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .servicio {
        width: 300px;
        height: 450px;
        margin: 2rem;
        overflow: hidden;
        p {
            padding: 1rem;
            letter-spacing: .1rem;
        }
        .btn{
        letter-spacing: .3rem;
        position: absolute; /* Imprescindible */
        width: 100vw;
        left: 50%; /* X */
        top: 100%; /* Y */
        transform: translate(-50%, -100%); /* Reposicionar X,Y */
        }
        img {
            box-shadow: 0 0 2rem $grisOscuroT;
            margin-left: 6.5rem;
            border: 1px solid $grisClaro;
            border-radius: 50%;
            width: 17rem;
            height: 17rem;
        }
    }
}
/* MediaQuerys */

@include desktop {
    .servicio {
        transform: scale(.85);
    }
}