html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;    
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_parrafos;
    font-size: 1.6rem;
    line-height: 1.8;
}
p {
    color: $white;
    letter-spacing: .23rem;
}
@include desktop {
    body {
        width: 100vw;
    height: 100vh;
    }
    
    .container{
        width: 100%;
        height: 100%;
        scroll-snap-type: y mandatory;
        overflow-y: scroll;
        
        section{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            scroll-snap-align: start;
        }
    }
}

.contenedor {
    width: 90%;
    margin: 0px auto;
}
a {
    text-decoration: none;
}
img,
picture {
    width: 100%;
    height: auto;
    display: block;
}
h1, h2, h3,h4 {
    color: $white;
    font-family: $fuente_headings;
    font-weight: $h_black;
    text-align: center;
    letter-spacing: .3rem;
}
label {
    color: $white;
    letter-spacing: .2rem;
}
h1 {
    font-size: 3.5rem;
}
h2 {
    font-size: 3rem;
}
h3 {
    font-size: 2.5rem;
}
h4 {
    font-size: 1.8rem;
}
.dark-mode {
    background-color: $grisOscuro;
}
.glass {
    backdrop-filter: blur(1rem);
    border: 2px solid $grisClaroT;
    box-shadow: 0 0 1rem $blackT;
    background-color: $grisClaroT;
    border-radius: 2rem;
}

@include telefono {
    p {
        font-size: 1.4rem;
    }
    h1 {
        font-size: 3rem;
    }
    h2 {
        font-size: 2.8rem;
    }
    h3 {
        font-size: 2.2rem;
    }
    h4 {
        font-size: 1.6rem;
    }
}
@include telefono-S {
    p {
        font-size: 1.2rem;
    }
    h1 {
        font-size: 2.8rem;
    }
    h2 {
        font-size: 2.6rem;
    }
    h3 {
        font-size: 2rem;
    }
    h4 {
        font-size: 1.4rem;
    }
}
@include telefono-SS {
    p {
        font-size: 1rem;
    }
    h1 {
        font-size: 2.6rem;
    }
    h2 {
        font-size: 2.4rem;
    }
    h3 {
        font-size: 1.8rem;
    }
    h4 {
        font-size: 1.2rem;
    }
}