.banner {
    position: relative;
    width: 100%;
    height:  100vh;
    background-size: cover;
    background-position: center;
    transition: all .5s ease-in-out;
    background-image:  url(..//img/hero-1.jpg);
    animation: banner 28s infinite linear;
    .banner-content {
        margin: 0;
        padding: 0;
        width: 100%;
        height: 100%;
        color:  $white;
        background-color: $grisOscuroT;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .banner-info {
        width: 90vw;
        p {
            color: $white;
            text-align:  center;
            font-size: 3rem;
            font-weight: $p_black;
        }
    }
    @keyframes banner {
        0%{
            background-image:  url(..//img/hero-1.jpg);
        }
        25%{
            background-image:  url(..//img/hero-1.jpg);
        }
        26%{
            background-image:  url(..//img/hero-2.jpg);
        }
        50%{
            background-image:  url(..//img/hero-2.jpg);
        }
        51%{
            background-image:  url(..//img/hero-3.jpg);
        }
        75%{
            background-image:  url(..//img/hero-3.jpg);
        }
        76%{
            background-image:  url(..//img/hero-4.jpg);
        }
        100%{
            background-image:  url(..//img/hero-4.jpg);
        }
    }
}

@include telefono {
    .banner-info {
        p {
            font-size: 1.6rem !important;
        }
    }
}