.obra {
    padding-bottom: 10rem;
}
.datos {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0rem;
    h3 {
        margin-top: 0;
        margin-bottom: 0;
    }
}
.galery {
    width: 100%;
    max-width: 120rem;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit,133px);
    grid-auto-rows: 200px;
    justify-content: center;
    gap: 1.2rem;
    .galery-i {
        picture {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        clip-path: polygon(50% 0%,100% 25%,100% 75%,50% 100%,0% 75%,0% 25% );
        grid-column: span 2;
        height: 280px;
        filter: brightness(0.4);
        &:hover{
            filter: none;
            transition: all 250ms ease-in-out;
        }
        &:first-of-type {
            grid-column: 2 / span 2;
        }
    }
}
/* media querrys*/
@media screen and (min-width:270px) and (max-width:504px){
    .galery {
        grid-auto-rows: 283px;
        .galery-i {
            &:first-of-type{
                grid-column: 1 / span 2;
            }
        }
    }
}
@media screen and (min-width:505px) and (max-width:685px){
    .galery {
        .galery-i {
            &:nth-of-type(odd) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:686px) and (max-width:862px){
    .galery {
        .galery-i {
            &:nth-of-type( 3n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }

}
@media screen and (min-width:863px) and (max-width:1038px){
    .galery {
        .galery-i {
            &:nth-of-type( 4n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:1039px) and (max-width:1215px){
    .galery {
        .galery-i {
            &:nth-of-type( 5n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:1216px) and (max-width:1391px){
    .galery {
        .galery-i {
            &:nth-of-type( 6n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}
@media screen and (min-width:1392px){
    .galery {
        .galery-i {
            &:nth-of-type( 7n + 1) {
                grid-column: 2 / span 2;
            } 
        }
    }
}

@include desktop{
    .obra {
        transform: scale(.8);
    }
}
@include tablet{
    .obra {
        transform: scale(.8);
    }
}