.seccion {
    margin-top: calc($m_separacion);
    margin-bottom: calc($m_separacion);
}
.contenido-centrado {
    max-width: 80rem;
}
.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}
.sombra{
    box-shadow: 0px 1px 15px 0px rgb(15 15 14 / 90%);
}
.red_sombra{
    box-shadow: 0px 1px 15px 0px rgb(135 17 17 / 80%);
}
.label-b{
  color: $black;
}
.boton {
    display: block;
    font-family: $fuente_headings;
    color: $white;
    text-align: center;
    padding: 1rem 2rem;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 2rem;
    border: none;
    &:hover {
        cursor: pointer;
    }
}
.pading-top {
  padding-top: 7rem;
}
.pading_top_2 {
  padding-top: 11rem;
}
/* background */


  .bg {
    animation:slide 4.5s ease-in-out infinite alternate;
    background-image: linear-gradient(-60deg, #871111 50%,  #333   50%);
    bottom:0;
    left:-50%;
    opacity:.5;
    position:fixed;
    right:-50%;
    top:0;
    z-index:-1;
  }
  .bg2 {
    animation-direction:alternate-reverse;
    animation-duration:5s;
  }
  
  .bg3 {
    animation-duration:6s;
  }
  @keyframes slide {
    0% {
      transform:translateX(-25%);
    }
    100% {
      transform:translateX(25%);
    }
  }